const getBunicornImage = function(element, star, bunicornId) {
  console.log(element, star, bunicornId);
  let bunicornImages;
  if (bunicornId < 42850) {
    bunicornImages = bunicornImages3;
  } else if (bunicornId < 45000) {
    bunicornImages = bunicornImages1;
  } else {
    bunicornImages = bunicornImages2;
  }
  let images = bunicornImages[element][parseInt(star)];
  return images[bunicornId % images.length];
};

function ElementString(e) {
  switch (e) {
    case 0:
      return "FIRE";
    case 1:
      return "EARTH";
    case 3:
      return "WATER";
    case 2:
      return "AIR";
    case 4:
      return "NEUTRAL";
    default:
      return "???";
  }
}

async function getBunicorn(bunicornId) {
  var bunicorn, bunicornStamina;
  await bunicornContract.methods
    .get(bunicornId)
    .call()
    .then((x) => (bunicorn = x));
  await bunicornContract.methods
    .getStaminaPoints(bunicornId)
    .call()
    .then((x) => (bunicornStamina = x));
  var n = (bunicorn._props >> 5) & 127,
    attr1Element = n % 5,
    attr2Element = Math.floor(n / 5) % 5,
    attr3Element = Math.floor(Math.floor(n / 5) / 5) % 5,
    element = (bunicorn._props >> 3) & 3,
    star = 7 & bunicorn._props;
  return {
    id: bunicornId,
    properties: bunicorn._props,
    element: ElementString(element),
    attr1: ElementString(attr1Element),
    attr1Value: bunicorn._attr1,
    attr1Type: attr1Element,
    attr2: ElementString(attr2Element),
    attr2Value: bunicorn._attr2,
    attr2Type: attr2Element,
    attr3: ElementString(attr3Element),
    attr3Value: bunicorn._attr3,
    attr3Type: attr3Element,
    level: bunicorn._level,
    star: star,
    stamina: bunicornStamina,
    bonusAttribute: bunicorn._bonusAttribute,
    image: getBunicornImage(ElementString(element), star, bunicornId),
  };
}

export async function getTrainer(trainerId) {
  var trainerGet, trainerStamina;
  await trainerContract.methods
    .get(trainerId)
    .call()
    .then((x) => (trainerGet = x));
  await trainerContract.methods
    .getStaminaPoints(trainerId)
    .call()
    .then((x) => (trainerStamina = x));

  return {
    id: trainerId,
    element: trainerGet._element,
    elementName: ElementString(parseInt(trainerGet._element)),
    level: trainerGet._level + 1,
    exp: trainerGet._exp,
    power: trainerGet._power,
    stamina: trainerStamina,
    image: getTrainerImage(
      ElementString(parseInt(trainerGet._element)),
      trainerId
    ),
  };
}

