export const getBunicornsAndTrainers = async function(address) {
  var bunicorns, trainers;
  await gameContract.methods
    .getMyBunicorns()
    .call({ from: address })
    .then((x) => (bunicorns = x));
  await gameContract.methods
    .getMyTrainers()
    .call({ from: address })
    .then((x) => (trainers = x));
  var bunicornInfo = {},
    trainerInfo = {};
  // for (let bunicorn of bunicorns) {
  //   await getBunicorn(bunicorn).then((x) => (bunicornInfo[x.id] = x));
  // }
  await Promise.all(
    bunicorns.map(async (bunicorn) => {
      await getBunicorn(bunicorn).then((x) => (bunicornInfo[x.id] = x));
    })
  );
  for (let trainer of trainers) {
    await getTrainer(trainer).then((x) => (trainerInfo[x.id] = x));
  }
  console.log("----", trainerInfo);
  return {
    trainers: trainerInfo,
    bunicorns: bunicornInfo,
  };
};

