<template>
  <v-container>
    <v-row>
      <v-col cols="12" sm="2"> </v-col>
      <v-col cols="12" sm="8">
        <v-sheet class="theme--light rounded-lg">
          <div v-if="items && items.length > 0" class="d-flex">
              : {{getWinningRatio(items)}}%
          </div>
          <ul>
            <ul
              style="position: relative; list-style: none; padding-left: 0px"
              id="log"
            >
              <div v-if="items && items.length <= 0" style="text-align: center;">
                <v-progress-circular
                  indeterminate
                  color="primary"
                ></v-progress-circular>
              </div>
              <li
                style="margin-bottom: 8px"
                v-for="item in items"
                v-bind:key="item.id"
              >
                <div
                  class="d-flex"
                  style="
                    text-align: center;
                    padding: 1em;
                    border-radius: 4px;
                    background: #a3cfec;
                    padding-left: 24px;
                    overflow: hidden;
                    jusify-content: center;
                    vertical-align: middle;
                  "
                  v-if="
                    item.myPower >= item.enemyPower &&
                    Object.keys(bunicorns).length > 0
                  "
                >
                  <trainer
                    :trainers="trainers"
                    :trainerId="item.trainerId"
                    :icons="icons"
                  />
                  <bunicorn
                    :bunicorns="bunicorns"
                    :item="item"
                    :icons="icons"
                  />
                  <div style="width: 30%">
                    <img
                      src="https://bunicorn.game/img/Vs.a87df2b8.png"
                      style="width: 100%"
                    />
                  </div>
                  <enemy :enemyId="item.enemyId" :item="item" :icons="icons" />
                  <div style="width: 15%">
                    <p class="font-weight-thin caption">{{getTimeText(item.timeStamp)}}</p>
                    <span>
                      <span style="font-weight: bold; line-height: 1.5">{{
                        item.reward.toString().slice(0, 6)
                      }}</span
                      ><img
                        style="vertical-align: middle"
                        data-v-e9d7369a=""
                        src="data:image/png;base64,"
                        class="bur-icon"
                    /></span>
                  </div>
                </div>
                <div
                  v-else-if="
                    item.myPower < item.enemyPower &&
                    Object.keys(bunicorns).length > 0
                  "
                  class="d-flex"
                  style="
                    text-align: center;
                    padding: 1em;
                    border-radius: 4px;
                    background: #e2b6b3;
                    padding-left: 24px;
                    overflow: hidden;
                    jusify-content: center;
                    vertical-align: middle;
                  "
                >
                  <trainer
                    :trainers="trainers"
                    :trainerId="item.trainerId"
                    :icons="icons"
                  />
                  <bunicorn
                    :bunicorns="bunicorns"
                    :item="item"
                    :icons="icons"
                  />
                  <div style="width: 30%">
                    <img
                      src="https://bunicorn.game/img/Vs.a87df2b8.png"
                      style="width: 100%"
                    />
                  </div>
                  <enemy :enemyId="item.enemyId" :item="item" :icons="icons" />
                  <div style="width: 15%">
                    <p class="font-weight-thin caption">{{getTimeText(item.timeStamp)}}</p>
                    <span>
                      <span style="font-weight: bold; line-height: 1.5">{{
                        item.reward.toString().slice(0, 6)
                      }}</span
                      ><img
                        style="vertical-align: middle"
                        data-v-e9d7369a=""
                        src="data:image/png;base64,"
                        class="bur-icon"
                    /></span>
                  </div>
                </div>
              </li>
            </ul>
          </ul>
        </v-sheet>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { getLogs, getBunicornsAndTrainers, icons } from "../utils";
import Bunicorn from "./Bunicorn.vue";
import Enemy from "./Enemy.vue";
import Trainer from "./Trainer.vue";
export default {
  name: "SearchLog",
  components: {
    bunicorn: Bunicorn,
    Trainer,
    Enemy,
  },
  data: () => ({
    items: [],
    bunicorns: {},
    icons: {},
  }),
  watch: {
    $route(to, from) {
      console.log(to, from);
      this.items = [];
      getLogs(this.$route.params.address).then((logs) => {
        this.items = logs;
      }).catch();
      getBunicornsAndTrainers(this.$route.params.address).then((props) => {
        this.bunicorns = props.bunicorns;
        this.trainers = props.trainers;
      });
    },
  },
  created() {
    console.log(this.$route.params);
    getLogs(this.$route.params.address).then((logs) => {
      this.items = logs;
    }).catch(
      () => alert('   .')
    );
    getBunicornsAndTrainers(this.$route.params.address).then((props) => {
      this.bunicorns = props.bunicorns;
      this.trainers = props.trainers;
    });
    this.icons = icons;
  },
  methods: {
    getWinningRatio: function(logs) {
      let r = logs.filter(e => e.myPower > e.enemyPower).length / logs.length;
      return Math.round(r * 10000) / 100
    },
    getTimeText: function (value) {
      console.log('v', value)
        const today = new Date();
        const timeValue = new Date(Number(value) * 1000);

        const betweenTime = Math.floor((today.getTime() - timeValue.getTime()) / 1000 / 60);
        if (betweenTime < 1) return '';
        if (betweenTime < 60) {
            return `${betweenTime}`;
        }

        const betweenTimeHour = Math.floor(betweenTime / 60);
        if (betweenTimeHour < 24) {
            return `${betweenTimeHour}`;
        }

        const betweenTimeDay = Math.floor(betweenTime / 60 / 24);
        if (betweenTimeDay < 365) {
            return `${betweenTimeDay}`;
        }

        return `${Math.floor(betweenTimeDay / 365)}`;
 }
  }
};


</script>