  <div style="font-size: 0.8em; width: 30%">
    <img style="width:80%" :src="getImage(enemyId)" />
    <div
      style="
        text-align: justify;
        padding-top: 1px;
        padding-bottom: 1px;
        margin: 0px auto;
        background: #151b2a;
        border-radius: 4px;
        width: 80%;
        color: white;
      "
    >
      <p
        style="
          margin-top: 2px;
          margin-left: 0.2em;
          margin-bottom: -4px;
          font-size: smaller;
        "
      >
        <!-- ID: -->
        <!-- <span style="color: #7effee">{{ enemyId }}</span> -->
      </p>
      <img
        style="width: 100%"
        data-v-33c8c501=""
        src="data:image/png;base64,"
        class="rectangle"
      />
      <p
        style="
          margin-top: 2px;
          margin-left: 0.2em;
          margin-bottom: 0px;
          font-size: smaller;
        "
      >
        <span><img style="width: 8px" :src="icons[getElement(enemyId)]" /></span
        >{{ "Power: " + item.enemyPower }}
      </p>
    </div>
  </div>
