<script>
import { getTrainerImage } from "../utils";
export default {
  name: "Enemy",
  data: () => ({}),
  props: ["enemyId", "item", "icons"],
  methods: {
    getElement: function (id) {
        
      switch (id.toString(16)[0]) {
        case "0":
          return "FIRE";
        case "1":
          return "EARTH";
        case "2":
          return "WATER";
        case "3":
          return "AIR";
        default:
          return "FIRE";
      }
    },
    getImage: function (id) {
      var i = getTrainerImage(this.getElement(id), id);
      console.log('i:', i)
      return i
    },
  },
};
</script>